.loadingWrapper {
  position: fixed;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.loading {
  display: inline-block;
  width: 104px;
  height: 104px;

  padding: 10px;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 1em;
}

.loading:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  /* margin: 8px; */
  border-radius: 50%;
  border: 20px solid var(--loading-color);
  border-color: var(--loading-color) transparent var(--loading-color) transparent;
  animation: loading 1.2s linear infinite;
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
  